import React, { Component } from "react";
import PropTypes from "prop-types";
import "./avatar.css";

//A class component that manages the avatar, uses the uploaded full scale image to produce a thumbnail
class Avatar extends Component {

	state = {
		avatarThumbnail: null
	}

	componentWillMount() {
		this.setState({avatarThumbnail: this.props.avatarplaceholder});
	}

	componentDidUpdate() {
		//Every update check if the fullsize image was updated
		this.checkFullSizeChanged();
	}

	//Check to see if the image was changed via the parent.
	//as determined by a boolean.
	//If changed then update the avatar thumbnail
	checkFullSizeChanged = () => {
		if(this.props.fullSizeAvatarJustUpdated) {
			this.props.thumbnailWasUpdated();
			this.fullSizeImageToThumbnail(this.props.fullSizeAvatar);
		}
	}

	//Changes the fullsize image into a jpeg via the canvas.
	//Note avatar image is locked to specific height not width.
	fullSizeImageToThumbnail = (imageData) => {
		//If completed pass image object into onImageLoad function
		this.convertImageDataToImage(imageData).then(this.onImageLoad);
	}

	//Promise representing the eventual completion of imageData to an image
	convertImageDataToImage = (imageData) => {
		return new Promise (function(resolve) {
			let tempImg = new Image();
			tempImg.src = imageData;
			tempImg.onload = () => {resolve(tempImg);};
		});
	}

	//Rescale the temporary image on a temporary canvas to the specified thumbnail height while keeping it's scale.
	onImageLoad = (tempImg) => {
		let tempCanvas = document.createElement("canvas");
		let ctx = tempCanvas.getContext("2d");

		//Changes image to display correctly in avatar container
		if(this.isPortraitImage(tempImg)){
			tempCanvas.width = Avatar.avatarThumbnailWidth;
			tempCanvas.height = tempImg.height / (tempImg.width / Avatar.avatarThumbnailWidth);
		} else {
			tempCanvas.width = tempImg.width / (tempImg.height / Avatar.avatarThumbnailHeight);
			tempCanvas.height = Avatar.avatarThumbnailHeight;
		}
		//Draw image onto canvas
		ctx.drawImage(tempImg, 0, 0, tempCanvas.width, tempCanvas.height);
		//Resize to thumbnail as a jpeg type file
		//A jpeg type file size is generally smaller than a png type file for the same image.
		let resizedImageData = tempCanvas.toDataURL("image/jpeg");
		this.updateThumnail(resizedImageData);
		}

	//Looks at dimensions and check if it is portrait and return true
	//If not, it is a square or landscape image
	isPortraitImage = (image) => {
		return image.width < image.height ? true : false;
	}

	//Update the avatar thumbnail in this components state
	//and send the string data back to HCardBUilder via Avatars parent.
	updateThumnail = (resizedImageData) => {
		this.setState({avatarThumbnail: resizedImageData});
		this.props.updateThumbnail(resizedImageData);
	}

	render() {
		return (
			<div id="avatarContainer">
				<img
					id="avatar"
					src={this.state.avatarThumbnail}
					alt="avatar thumbnail"
				/>
			</div>
		);
	}
}

Avatar.avatarThumbnailHeight = 104;//static final/constant value (in pixels)
Avatar.avatarThumbnailWidth = 84;//static final/constant value (in pixels)

export default Avatar;

Avatar.propTypes = {
		avatarplaceholder: PropTypes.string,
		fullSizeAvatar: PropTypes.string,
		fullSizeAvatarJustUpdated: PropTypes.bool,
		thumbnailWasUpdated: PropTypes.func,
		updateThumbnail: PropTypes.func
	}


