import React, { Component } from "react";
import PropTypes from "prop-types";
import "./previewsubsection.css";

//Class component that contains a majority of the text fields of the preview section.
class PreviewSubSection extends Component {

	// Display text with comma if defined as per JSON formView file
	// Test to display comma after selected text:
	//	- text written after text which will have comma has to be written
	//	- text which will have comma has been written
	displayTextWithComma = (infoPart,detail,index) => {
			return ( this.props.formData[detail] !== ""
					&& this.props.formData[infoPart.line[index+1]] !== "")
					? true : false;
	}

	render() {
		return (
			<div id="previewDiv">
				<div id="innerPreviewDiv">
				 	{this.props.formView.sections.map((infoPart, index) => {
					 	return(
					 		<div className={infoPart.className} key={infoPart.label + index}>
					 			<div className="labelPreviewInfoContainer">
					 				<h2 className="infoPreviewPartLabel">{infoPart.label}</h2>
					 			</div>
					 			<div className="textPreviewInfoContainer">
					 				{infoPart.line.map((detail, index) => {
					 					return(
											<span className="infoPreviewPartText" key={detail + index}>
												{(infoPart.commaAfter.indexOf(detail) > -1
												&& this.displayTextWithComma(infoPart, detail, index))
												? `${this.props.formData[detail]}, `
												: `${this.props.formData[detail]} `}
											</span>
										)
					 				})}
					 			</div>
					 		</div>
					 	)
					})}
				</div>
			</div>
		);
	}
}

export default PreviewSubSection;

PreviewSubSection.propTypes = {
	formView: PropTypes.object,
	formData: PropTypes.object
}


