import React, { Component } from "react";
import "babel-polyfill";//Needed for React IE10+ compatibility
//import react components
import Builder from "./builder/Builder.js";
import Preview from "./preview/Preview.js";
//Form definition for the builder area
import builderForm from "./builder/builderForm.json";
//Form definition for the preview area
import previewForm from"./preview/previewForm.json";
import avatarplaceholder from "./preview/avatarPlaceholder.png";

import "./hcardbuilder.css"

//Class component of the HCard Builder
//The builder and preview section text fields can be altered with ease
//by updating the builder and preview form JSON definitions
class HCardBuilder extends Component {

	state = {
		fullSizeAvatar: "",
		fullSizeAvatarJustUpdated: false,
		avatar: "",
	}

	componentWillMount(){
		this.setCardValuesToEmpty();
	}

	//Create the intial state of the card data which are key value pairs with the values set to ""
	setCardValuesToEmpty = () => {
		let data = {};
		builderForm.sections.forEach(section => {
			section.inputs.forEach(input => {
				data[input.name] = "";
			});
		});
		this.setState({formData: data});
	}

	//Update form data in state.
	//When this is called it indicates there has been a change in text input.
	formDataUpdated = (formData) => {
		this.setState({formData: formData});
	}

	//Indicate that the fullsize avatar was updated
	updateFullSizeAvatar = (fullSizeAvatar) => {
		this.setState({
			fullSizeAvatar: fullSizeAvatar,
			fullSizeAvatarJustUpdated: true
		});
	}
	//If thumnail was updated then change state of just updated to false
	//No need for the fullSize Avatar in memory so it is set to ""
	thumbnailWasUpdated = () => {
		this.setState({
			fullSizeAvatarJustUpdated: false,
			fullSizeAvatar: ""
		});
	}

	//Send hCard object to console for viewing
	//Create alert and inform user
	//Note: The hCard create action is out of scope for this project
	createHCard = () => {
		let hCardDataForServer = {
				formdata:this.state.formData,
				avatar:this.state.avatar
			};
		console.log("The following hCardData object would be sent to the server for processing.");
		console.log(hCardDataForServer);
		alert("hCardData object (for server) was just logged in the console.");
		alert("Note: The hCard action is out of scope for this project.");
	}

	//Update thumbnail state with thumbnail string
	updateThumbnail = (thumbnail) => {
		this.setState({avatar: thumbnail});
	}

	render() {
		return (
			<div id="HCardBuilder">
				<Builder
					formView={builderForm}
					updateFullSizeAvatar={this.updateFullSizeAvatar}
					createHCard={this.createHCard}
					formDataToParent={this.formDataUpdated}
				/>
				<Preview
					formView={previewForm}
					formData={this.state.formData}
					fullSizeAvatar={this.state.fullSizeAvatar}
					avatarplaceholder={avatarplaceholder}
					fullSizeAvatarJustUpdated={this.state.fullSizeAvatarJustUpdated}
					thumbnailWasUpdated={this.thumbnailWasUpdated}
					updateThumbnail={this.updateThumbnail}
				/>
			</div>
		);
	}
}

export default HCardBuilder;



