import React from "react";
import PropTypes from "prop-types";
import BuilderSubSection from "./BuilderSubSection.js"
import BuilderButtons from "./BuilderButtons.js"
import "./builder.css"

//Component Builder using a stateless functional component
//Builder contains the form with the input text boxes and buttons
function Builder(props){
	return(
		<div id="builderContainer">
			<form id="innerBuilderContainer">
				<h1 id="builderHeading">hCard Builder</h1>
				<BuilderSubSection
					formView={props.formView}
					formDataToParent={props.formDataToParent}
				/>
				<BuilderButtons
					updateFullSizeAvatar={props.updateFullSizeAvatar}
					createHCard={props.createHCard}
				/>
			</form>
		</div>
	)
}

export default Builder;

Builder.propTypes = {
		formView: PropTypes.object,
		setThumbnail: PropTypes.func,
		createHCard: PropTypes.func,
		formDataToParent: PropTypes.func
	}


