import React, { Component } from "react";
import PropTypes from "prop-types";
import "./builderbuttons.css"

//Class component that contains the two action buttons
class BuilderButtons extends Component {

	//When the "Upload Avatar" button is clicked it fires on the file type input element.
	triggerInputFile = () => {
		this.fileInput.click();
	}

	//Get the image source file and turn into thumbnail
	fileHandler = (event) => {
		let file = event.target.files[0];
		if(this.isImageFile(file)){
			this.convertTargetToImageData(file)
				.then(this.props.updateFullSizeAvatar)
				.catch(()=>{alert("Error: Try upload or use another image");});
		}else {
	        alert("Error: Not an image file. Please upload a image type file.");
	    }
	}

	//Promise representing the eventual completion of the target into a imagedata/string
	convertTargetToImageData = (file) => {
		return new Promise (function(resolve, reject) {
			//create reader object
			let reader = new FileReader();
			reader.readAsDataURL(file);
			reader.onload = () => {resolve(reader.result)};
			reader.onerror = () =>{reject()};
		});
	}

	//Check if the 'key' exists in the file object and the file type is an image type
	isImageFile = (file) =>{
	 	return (('type' in file) && file.type.includes("image"))
	 			? true : false;
	}

	render(){
		return(
			<div style={{ "display": "flex", "flexWrap": "wrap", "width":"100%"}}>
				<div className="builderButtonContainer">
					<input
					    ref={fileInput => this.fileInput = fileInput}
					    type="file"
					    style={{"display":"none"}}
					    onChange={event => this.fileHandler(event)}
					 />
					<input
						type="button"
						id="builderAvatarButton"
						className="buttonBuilder"
						value="Upload Avatar"
						onClick={this.triggerInputFile}
						/>
				</div>
				<div className="builderButtonContainer">
					<input
						type="button"
						id="builderSubmitButton"
						className="buttonBuilder"
						value="Create hCard"
						onClick={this.props.createHCard}
					/>
				</div>
			</div>
		)
	}
}

export default BuilderButtons;

BuilderButtons.propTypes = {
		createHCard: PropTypes.func,
		updateImage: PropTypes.func
	}



