import React, { Component } from "react";
import PropTypes from "prop-types";
import "./builder.css"
import "./buildersubsection.css"

//Class component that contains an organized layout of input text boxes
class BuilderSubSection extends Component {

	//Called before first render
	componentWillMount() {
		this.setFormValuesToEmpty();
	}

	//Create the intial state of the formData object which are key-value pairs with the values set to ""
	setFormValuesToEmpty = () => {
		let data = {};
		this.props.formView.sections.forEach(section => {
			section.inputs.forEach(input => {data[input.name] = "";})
		});
		this.setState({formData: data});
	}

	//If a input field is changed then update the formData key-value pair.
	inputHandler = (key, value) => {
		this.setState(prevState => {
			prevState.formData[key] = value;
			this.props.formDataToParent(prevState.formData);//Send up
			return prevState;
		});
	}

	render(){
		return(
			<div style={{width:'100%'}}>
				{this.props.formView.sections.map((section, index1) => {
					return(
						<div key={index1} className="builderSectionContainer">
							<h2 className="builderSectionTitle">{section.title}</h2>
							{section.inputs.map((input,index2) => {
								return(
									<div key={input.name + index2} className="builderInputOuterContainer">
										<div className="inputInputInnerContainer">
											<label
												className="builderInputlabel"
												htmlFor={input.name}
											>
												{input.title}
											</label>
											<br/>
								            <input
												className="builderInputText"
												id={input.name}
												type="text"
												placeholder={input.placeholder}
												value={this.state.formData[input.name]}
												onChange={event => this.inputHandler(input.name, event.target.value)}
								            />
							           	</div>
									</div>
								)
							})}
						</div>
					)
				})}
			</div>
		)
	}
}

export default BuilderSubSection;

BuilderSubSection.propTypes = {
		form: PropTypes.object,
		formDataToParent: PropTypes.func
	}


