import React from "react";
import PropTypes from "prop-types";
import Avatar from "./Avatar.js";
import PreviewSubSection from "./PreviewSubSection.js";
import "./preview.css";

//A functional component that contains the preview sections of the hCard
function Preview(props) {
	return (
		<div id="previewContainer">
			<div id="innerPreviewContainer" className="vCard">
				<h2 id="previewTitle">HCARD PREVIEW</h2>
				<div id="previewTopSection">
					<h2 id="previewName">
						{`${props.formData.firstName} ${props.formData.lastName}`}
					</h2>
					<Avatar
						avatarplaceholder={props.avatarplaceholder}
						fullSizeAvatar={props.fullSizeAvatar}
						fullSizeAvatarJustUpdated={props.fullSizeAvatarJustUpdated}
						thumbnailWasUpdated={props.thumbnailWasUpdated}
						updateThumbnail ={props.updateThumbnail}
					/>
				</div>
					<PreviewSubSection
						formView={props.formView}
						formData={props.formData}

					/>
			</div>
		</div>
	);
}

export default Preview;

Preview.propTypes = {
		formView: PropTypes.object,
		formData: PropTypes.object,
		fullSizeAvatar: PropTypes.string,
		avatarplaceholder: PropTypes.string,
		fullSizeAvatarJustUpdated: PropTypes.bool,
		thumbnailWasUpdated: PropTypes.func,
		updateThumbnail: PropTypes.func
	}


